// src/pages/Start.js
import React, { useEffect, useMemo, useState } from "react";
import {
    Container,
    Box,
    Typography,
    Stack,
    Button,
    alpha,
    Chip,
} from "@mui/material";
import { motion, useScroll, useTransform, useSpring } from "framer-motion";
import { ArrowOutward, CalendarMonth } from "@mui/icons-material";

/* ------------------------- Instagram (2-Klick) ------------------------- */
function InstagramAktuelles() {
    const [enabled, setEnabled] = React.useState(false);

    React.useEffect(() => {
        if (!enabled) return;

        if (!window.instgrm) {
            const script = document.createElement("script");
            script.src = "https://www.instagram.com/embed.js";
            script.async = true;
            script.onload = () => window.instgrm?.Embeds?.process?.();
            document.body.appendChild(script);
        } else {
            window.instgrm?.Embeds?.process?.();
        }
    }, [enabled]);

    return (
        <Box
            sx={{
                p: { xs: 2.2, md: 3.5 },
                borderRadius: 4,
                background: alpha("#FFFFFF", 0.85),
                border: `1px solid ${alpha("#0B1B24", 0.10)}`,
                boxShadow: "0 18px 50px rgba(11,27,36,0.10)",
                backdropFilter: "blur(12px)",
                height: "100%",
            }}
        >
            <Typography variant="h4" sx={{ mb: 1.2 }}>
                Aktuelles
            </Typography>

            {!enabled ? (
                <>
                    <Typography sx={{ color: "text.secondary", mb: 2, lineHeight: 1.8 }}>
                        Neuigkeiten und Eindrcke aus unserem Instagram-Account.
                    </Typography>
                    <Button variant="contained" disableElevation onClick={() => setEnabled(true)}>
                        Instagram-Inhalte laden
                    </Button>
                </>
            ) : (
                <Box
                    sx={{
                        display: "flex",
                        justifyContent: "center",
                        "& .instagram-media": {
                            margin: "0 !important",
                            maxWidth: "540px",
                            width: "100%",
                        },
                    }}
                >
                    <blockquote
                        className="instagram-media"
                        data-instgrm-permalink="https://www.instagram.com/tsc.wuelfrath/"
                        data-instgrm-version="14"
                        style={{ background: "#FFF", border: 0 }}
                    />
                </Box>
            )}
        </Box>
    );
}

/* --------------------------- Kalender helpers -------------------------- */
const addDays = (d, n) => {
    const x = new Date(d);
    x.setDate(x.getDate() + n);
    return x;
};

const startOfDay = (d) => new Date(d.getFullYear(), d.getMonth(), d.getDate());
const pad2 = (n) => String(n).padStart(2, "0");

const dayKeyLocal = (d) => {
    const x = startOfDay(d);
    return `${x.getFullYear()}-${pad2(x.getMonth() + 1)}-${pad2(x.getDate())}`;
};

const parseISODateLocal = (iso) => {
    const [y, m, day] = String(iso).split("-").map(Number);
    return new Date(y, (m || 1) - 1, day || 1);
};

const norm = (s) => String(s || "").trim().toLowerCase();

function startOfIsoWeek(d) {
    const x = startOfDay(d);
    const day = x.getDay(); // 0 So ... 1 Mo ... 6 Sa
    const diff = (day === 0 ? -6 : 1 - day);
    return addDays(x, diff);
}

const weekdayLabel = (d) => d.toLocaleDateString("de-DE", { weekday: "long" });
const dateLabel = (d) => d.toLocaleDateString("de-DE", { day: "2-digit", month: "2-digit" });

const TypeChip = ({ kind }) => {
    const label = kind === "training" ? "Training" : "Event";
    const bg = kind === "training" ? alpha("#27C2D3", 0.14) : alpha("#063A52", 0.12);
    return (
        <Chip
            size="small"
            label={label}
            sx={{
                height: 22,
                fontSize: 12,
                fontWeight: 800,
                background: bg,
                border: `1px solid ${alpha("#0B1B24", 0.10)}`,
            }}
        />
    );
};

const CancelChip = () => (
    <Chip
        size="small"
        label="Abgesagt"
        sx={{
            height: 22,
            fontSize: 12,
            fontWeight: 900,
            color: alpha("#0B1B24", 0.78),
            background: alpha("#0B1B24", 0.06),
            border: `1px dashed ${alpha("#0B1B24", 0.26)}`,
        }}
    />
);

function buildEventsInRange({ data, rangeStart, rangeDays }) {
    const start = startOfDay(rangeStart);
    const end = addDays(start, rangeDays);

    const wiederkehrend = Array.isArray(data?.wiederkehrend) ? data.wiederkehrend : [];
    const termine = Array.isArray(data?.termine) ? data.termine : [];

    const cancels = new Set(
        termine
            .filter((t) => t && t.type === "cancel" && t.date && t.title)
            .map((c) => `${String(c.date).trim()}|${norm(c.title)}`)
    );

    const events = [];

    // recurring
    for (let i = 0; i < rangeDays; i++) {
        const d = addDays(start, i);
        const weekday = d.getDay();
        wiederkehrend.forEach((r) => {
            if (weekday === r.weekday) {
                const title = r.title || "Training";
                const key = `${dayKeyLocal(d)}|${norm(title)}`;
                events.push({
                    kind: r.kind || "training",
                    title,
                    time: r.time || "",
                    location: r.location || "",
                    date: new Date(d),
                    cancelled: cancels.has(key),
                });
            }
        });
    }

    // events
    termine
        .filter((t) => t && t.type === "event" && t.date)
        .forEach((t) => {
            const d = parseISODateLocal(t.date);
            if (d >= start && d < end) {
                const title = t.title || "Termin";
                const key = `${dayKeyLocal(d)}|${norm(title)}`;
                events.push({
                    kind: t.kind || "event",
                    title,
                    time: t.time || "",
                    location: t.location || "",
                    date: d,
                    cancelled: cancels.has(key),
                });
            }
        });

    events.sort((a, b) => a.date - b.date);
    return events;
}

function groupEventsByDay({ events, rangeStart, rangeDays }) {
    const map = new Map();
    events.forEach((e) => {
        const k = dayKeyLocal(e.date);
        if (!map.has(k)) map.set(k, []);
        map.get(k).push(e);
    });

    const days = [];
    const start = startOfDay(rangeStart);
    for (let i = 0; i < rangeDays; i++) {
        const d = addDays(start, i);
        const k = dayKeyLocal(d);
        days.push({ date: d, key: k, events: map.get(k) || [] });
    }
    return days;
}

/* ---------------------------------------------------------------------- */

export default function Start({ onNavigate }) {
    const [heroImages, setHeroImages] = useState([]);
    const [currentImage, setCurrentImage] = useState(0);

    const [content, setContent] = useState(null);
    const [error, setError] = useState("");

    const contentRef = React.useRef(null);
    const [highlightContent, setHighlightContent] = React.useState(false);

    // Parallax
    const { scrollY } = useScroll();
    const parallaxRaw = useTransform(scrollY, [0, 700], [0, 80]);
    const parallaxY = useSpring(parallaxRaw, { stiffness: 120, damping: 20, mass: 0.2 });

    // Kalender (aktuelle Woche MoSo)
    const [weekDays, setWeekDays] = useState([]);
    const todayKey = useMemo(() => dayKeyLocal(new Date()), []);

    useEffect(() => {
        fetch(process.env.PUBLIC_URL + "/resources/start/startImages.json")
            .then((res) => res.json())
            .then((data) => setHeroImages(data.map((img) => process.env.PUBLIC_URL + `/resources/start/${img}`)))
            .catch(() => setHeroImages([]));
    }, []);

    useEffect(() => {
        fetch(process.env.PUBLIC_URL + "/content/start.json")
            .then((res) => {
                if (!res.ok) throw new Error(`HTTP ${res.status} beim Laden von /content/start.json`);
                return res.json();
            })
            .then(setContent)
            .catch((e) => setError(String(e)));
    }, []);

    // Auto-Advance Hero
    const hasImages = heroImages.length > 0;
    useEffect(() => {
        if (!hasImages) return;
        const t = setInterval(() => setCurrentImage((p) => (p + 1) % heroImages.length), 6500);
        return () => clearInterval(t);
    }, [hasImages, heroImages.length]);

    const bgImage = useMemo(() => (hasImages ? heroImages[currentImage] : null), [hasImages, heroImages, currentImage]);

    // Kalenderdaten laden (MoSo)
    useEffect(() => {
        const start = startOfIsoWeek(new Date());

        fetch(process.env.PUBLIC_URL + "/content/termine.json")
            .then((r) => {
                if (!r.ok) throw new Error(`HTTP ${r.status} beim Laden von /content/termine.json`);
                return r.json();
            })
            .then((data) => {
                const events = buildEventsInRange({ data, rangeStart: start, rangeDays: 7 });
                const grouped = groupEventsByDay({ events, rangeStart: start, rangeDays: 7 });
                setWeekDays(grouped);
            })
            .catch(() => setWeekDays([]));
    }, []);

    const onMore = () => {
        contentRef.current?.scrollIntoView({ behavior: "smooth" });
        setTimeout(() => {
            setHighlightContent(true);
            setTimeout(() => setHighlightContent(false), 900);
        }, 400);
    };

    if (error) return <div style={{ padding: 16 }}>Fehler: {error}</div>;
    if (!content) return <div style={{ padding: 16 }}>Lade Inhalte</div>;

    return (
        <Box>
            {/* Hero */}
            <Box
                sx={{
                    position: "relative",
                    minHeight: { xs: 480, md: 560 },
                    display: "flex",
                    alignItems: "flex-end",
                    overflow: "hidden",
                    borderRadius: { xs: 0, md: 6 },
                    mx: { xs: 0, md: 3 },
                    boxShadow: { xs: "none", md: "0 26px 70px rgba(6,58,82,0.20)" },
                }}
            >
                <Box
                    component={motion.div}
                    key={currentImage}
                    initial={{ opacity: 0 }}
                    animate={{ opacity: 1 }}
                    transition={{ duration: 0.8 }}
                    style={{ y: parallaxY }}
                    sx={{
                        position: "absolute",
                        inset: 0,
                        transform: "scale(1.06)",
                        background: bgImage
                            ? `url(${bgImage})`
                            : `linear-gradient(135deg, rgba(6,58,82,1), rgba(39,194,211,1))`,
                        backgroundSize: "cover",
                        backgroundPosition: "center",
                        willChange: "transform",
                    }}
                />

                <Box
                    sx={{
                        position: "absolute",
                        inset: 0,
                        background:
                            "linear-gradient(180deg, rgba(0,0,0,0.20) 0%, rgba(0,0,0,0.62) 78%, rgba(0,0,0,0.72) 100%)",
                        pointerEvents: "none",
                        zIndex: 1,
                    }}
                />

                <Box
                    sx={{
                        position: "absolute",
                        inset: 0,
                        background:
                            "radial-gradient(700px 400px at 20% 20%, rgba(39,194,211,0.25), transparent 55%)",
                        pointerEvents: "none",
                        zIndex: 2,
                    }}
                />

                <Container maxWidth="lg" sx={{ pb: { xs: 5, md: 7 }, position: "relative", zIndex: 3 }}>
                    <Box sx={{ maxWidth: 820 }}>
                        <Typography variant="h2" sx={{ color: "white", mb: 1.2, lineHeight: 1.05 }}>
                            {content.headline}
                        </Typography>
                        <Typography variant="h6" sx={{ color: alpha("#fff", 0.86), mb: 2.6, fontWeight: 500 }}>
                            {content.slogan}
                        </Typography>

                        <Stack direction={{ xs: "column", sm: "row" }} spacing={1.2}>
                            <Button
                                variant="contained"
                                disableElevation
                                endIcon={<ArrowOutward />}
                                sx={{ px: 2.2, py: 1.1, background: "linear-gradient(135deg, #063A52, #27C2D3)" }}
                                onClick={onMore}
                            >
                                Mehr erfahren
                            </Button>

                            <Button
                                variant="outlined"
                                sx={{
                                    px: 2.2,
                                    py: 1.1,
                                    color: "white",
                                    borderColor: alpha("#fff", 0.28),
                                    background: alpha("#000", 0.12),
                                    "&:hover": { borderColor: alpha("#fff", 0.40), background: alpha("#000", 0.22) },
                                }}
                                onClick={() => onNavigate?.("Kontakt")}
                            >
                                Kontakt aufnehmen
                            </Button>
                        </Stack>
                    </Box>
                </Container>
            </Box>

            {/* Willkommen + Aktuelles */}
            <Container
                ref={contentRef}
                maxWidth="lg"
                component={motion.div}
                initial={false}
                animate={{
                    y: highlightContent ? -2 : 0,
                    scale: highlightContent ? 1.003 : 1,
                    background: highlightContent
                        ? "radial-gradient(600px 300px at 50% 0%, rgba(39,194,211,0.22), transparent 70%)"
                        : "radial-gradient(600px 300px at 50% 0%, rgba(39,194,211,0), transparent 70%)",
                }}
                transition={{
                    y: { duration: 0.35, ease: "easeOut" },
                    scale: { duration: 0.35, ease: "easeOut" },
                    background: { duration: 0.6, ease: "easeOut" },
                }}
                sx={{ mt: { xs: 3, md: 5 }, borderRadius: 4, pb: 0.5 }}
            >
                <Box
                    sx={{
                        display: "grid",
                        gridTemplateColumns: { xs: "1fr", md: "2fr 1fr" },
                        gap: { xs: 2, md: 3 },
                        alignItems: "stretch",
                    }}
                >
                    <Box
                        sx={{
                            p: { xs: 2.2, md: 3.5 },
                            borderRadius: 4,
                            background: alpha("#FFFFFF", 0.85),
                            border: `1px solid ${alpha("#0B1B24", 0.10)}`,
                            boxShadow: "0 18px 50px rgba(11,27,36,0.10)",
                            backdropFilter: "blur(12px)",
                        }}
                    >
                        <Typography variant="h4" sx={{ mb: 1.5 }}>
                            Willkommen beim TSC Wlfrath
                        </Typography>

                        <Typography sx={{ color: "text.secondary", lineHeight: 1.9, fontSize: { xs: "1rem", md: "1.05rem" } }}>
                            {content.bodyText.map((line, i) => (
                                <Box key={i} component="p" sx={{ m: 0, mb: 1.2 }}>
                                    {line}
                                </Box>
                            ))}
                        </Typography>
                    </Box>

                    <Box sx={{ height: "100%", position: { md: "sticky" }, top: { md: 90 }, alignSelf: { md: "start" } }}>
                        <InstagramAktuelles />
                    </Box>
                </Box>
            </Container>

            {/* Kalender: vertikal, aber 3 Spalten nebeneinander */}
            <Container maxWidth="lg" sx={{ mt: { xs: 3, md: 4 } }}>
                <Box
                    sx={{
                        p: { xs: 2.2, md: 3.2 },
                        borderRadius: 4,
                        background: alpha("#FFFFFF", 0.88),
                        border: `1px solid ${alpha("#0B1B24", 0.10)}`,
                        boxShadow: "0 18px 55px rgba(11,27,36,0.10)",
                        backdropFilter: "blur(12px)",
                    }}
                >
                    <Stack
                        direction={{ xs: "column", sm: "row" }}
                        spacing={1.2}
                        alignItems={{ xs: "stretch", sm: "center" }}
                        justifyContent="space-between"
                        sx={{ mb: 1.4 }}
                    >
                        <Stack direction="row" spacing={1} alignItems="center">
                            <Box
                                sx={{
                                    width: 42,
                                    height: 42,
                                    borderRadius: 999,
                                    display: "grid",
                                    placeItems: "center",
                                    background: alpha("#27C2D3", 0.12),
                                    border: `1px solid ${alpha("#27C2D3", 0.22)}`,
                                }}
                            >
                                <CalendarMonth />
                            </Box>
                            <Box>
                                <Typography variant="h4" sx={{ lineHeight: 1.1 }}>
                                    Termine
                                </Typography>
                                <Typography variant="body2" sx={{ color: "text.secondary" }}>
                                    Diese Woche (MoSo)
                                </Typography>
                            </Box>
                        </Stack>

                        <Button variant="outlined" onClick={() => onNavigate?.("Termine")}>
                            Alle anzeigen
                        </Button>
                    </Stack>

                    {/* Columns Layout: Tage laufen nach unten, aber mehrere Spalten */}
                    <Box
                        sx={{
                            columns: { xs: 1, sm: 2, md: 3 },
                            columnGap: 12 / 8 + "rem", // ~1.5rem
                        }}
                    >
                        {weekDays.map((day) => {
                            const isToday = day.key === todayKey;

                            return (
                                <Box
                                    key={day.key}
                                    sx={{
                                        breakInside: "avoid",
                                        mb: 1.5,
                                        p: 1.6,
                                        borderRadius: 2,
                                        border: `1px solid ${alpha("#0B1B24", isToday ? 0.16 : 0.08)}`,
                                        background: alpha("#fff", 0.70),
                                        position: "relative",
                                        overflow: "hidden",
                                        boxShadow: isToday ? "0 18px 55px rgba(39,194,211,0.22)" : "none",
                                        transform: isToday ? "translateY(-1px)" : "none",
                                    }}
                                >
                                    {/* Today accent: krftiger, aber edel */}
                                    {isToday && (
                                        <>
                                            <Box
                                                sx={{
                                                    position: "absolute",
                                                    left: 0,
                                                    top: 0,
                                                    bottom: 0,
                                                    width: 6,
                                                    background: "#27C2D3",
                                                    opacity: 0.95,
                                                }}
                                            />
                                            <Box
                                                sx={{
                                                    position: "absolute",
                                                    inset: 0,
                                                    background:
                                                        "radial-gradient(420px 240px at 10% 15%, rgba(39,194,211,0.18), transparent 60%)",
                                                    pointerEvents: "none",
                                                }}
                                            />
                                        </>
                                    )}

                                    <Stack spacing={0.8} sx={{ pl: isToday ? 1 : 0 }}>
                                        <Stack direction="row" spacing={1} alignItems="center" sx={{ flexWrap: "wrap" }}>
                                            <Typography sx={{ fontWeight: 950, textTransform: "capitalize" }}>
                                                {weekdayLabel(day.date)}
                                            </Typography>
                                            <Typography variant="body2" sx={{ color: "text.secondary" }}>
                                                {dateLabel(day.date)}
                                            </Typography>
                                            {isToday && (
                                                <Chip
                                                    size="small"
                                                    label="Heute"
                                                    sx={{
                                                        height: 24,
                                                        fontSize: 12,
                                                        fontWeight: 900,
                                                        color: "#063A52",
                                                        background: alpha("#27C2D3", 0.20),
                                                        border: `1px solid ${alpha("#27C2D3", 0.42)}`,
                                                    }}
                                                />
                                            )}
                                        </Stack>

                                        {day.events.length === 0 ? (
                                            <Typography variant="body2" sx={{ color: "text.secondary" }}>
                                                Keine Termine
                                            </Typography>
                                        ) : (
                                            <Stack spacing={1} sx={{ mt: 0.6 }}>
                                                {day.events.map((e, idx) => (
                                                    <Box key={idx} sx={{ display: "flex", gap: 1, alignItems: "flex-start" }}>
                                                        <Stack direction="row" spacing={0.8} alignItems="center" sx={{ mt: "1px" }}>
                                                            <TypeChip kind={e.kind} />
                                                            {e.cancelled && <CancelChip />}
                                                        </Stack>

                                                        <Box sx={{ minWidth: 0 }}>
                                                            <Typography
                                                                sx={{
                                                                    fontWeight: 850,
                                                                    lineHeight: 1.2,
                                                                    textDecoration: e.cancelled ? "line-through" : "none",
                                                                    opacity: e.cancelled ? 0.65 : 1,
                                                                }}
                                                            >
                                                                {e.title}
                                                            </Typography>
                                                            <Typography
                                                                variant="body2"
                                                                sx={{ color: "text.secondary", opacity: e.cancelled ? 0.65 : 1 }}
                                                            >
                                                                {(e.time || "").trim()}
                                                                {e.location ? `  ${e.location}` : ""}
                                                            </Typography>
                                                        </Box>
                                                    </Box>
                                                ))}
                                            </Stack>
                                        )}
                                    </Stack>
                                </Box>
                            );
                        })}
                    </Box>
                </Box>
            </Container>
        </Box>
    );
}
